// ...........footer.............

.footer-content {
    padding-top: 45px;
    padding-bottom: 45px;
    background-color: $primary;
    color: color-yiq($primary);

    .single-footer {
        h5 {
            font-size: $font-size-lg;
            text-transform: uppercase;
            margin: 0;
        } 
        hr {
            margin-top: 5px;
            margin-bottom: 1rem;
            border: 0;
            border-top: $border-width solid $border-color;
            &:before {
                height: 1px;
                background-color: $secondary;
                content: "";
                position: absolute;
                width: 85px;
                margin-top: -1px;
            }
        }     
        ul{
            li{
                a{
                    .fa{
                        padding-right: 5px;
                    }
                    &:hover{
                        color: $secondary;
                    }
                }
            }
        }        
        .contact-list  {
            li {
                display: flex;
                align-items: center;
                list-style: none;
                font-size: $font-size-base;
                margin-bottom: 15px;

                .fas {
                    display: flex;
                    align-items: center;
                    justify-content: start;

                    font-size: $font-size-lg;
                    
                    color: $secondary;
                    height: 30px;
                    width: 30px;
                }
                a {
                    color: color-yiq($primary);;
                }
            }
        }
        .links-list{
            li{
                list-style-type: none;
                margin-bottom: 8px;
                a{
                    display: block;
                    font-size: $font-size-base;
                    color: color-yiq($primary);;
                    &:hover {
                        color: $secondary;
                    }
                }
            }
        }
               
               
        .newsletter {
            margin-bottom: 30px;
            .block {
                margin-bottom: 15px;
                position: relative;
                input {
                    border-radius: $border-radius;
                    height: 35px;
                    width: 100%;
                    color: $primary;
                    border: none;
                    background: $white;
                    padding: 0px 95px 0 15px;
                    font-size: $font-size-base;
                    overflow: visible;
                    box-shadow: none;
                    outline: none;
                }
                .btn {
                    position: absolute;
                    right: 1px;
                    top: 1px;
                    height: 33px;
                    padding: 0px 0px;
                    border-radius: $border-radius;
                    width: 90px;
                    box-shadow: none;
                    outline: none;
            
                    .btn-hover{
                        transition-duration: 5s;
                        display: none;
                        text-align: center;
                    }
                    &:hover {
                        .btn-hover{
                            display: block;
                            /* width: 131px; */
                            transition-duration: 5s;
                            
                        }
                        .btn-base{
                            display: none;
                            transition-duration: 5s;
                        }
                    }
                }
            }
        }
         
        // -----------------social
        .socials {
            display: block;
            .list {
                display: block;
                padding-left: 0;
                margin-bottom: 0;
                li{
                    display: inline-block;
                    a {
                        background-color: $primary;
                        color: color-yiq($primary);
                        font-size: $font-size-lg;
                        width: 30px;
                        height: 30px;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        text-decoration: none;
                        border-radius: $border-radius;
                        border: $border-width solid $border-color;

                        &:hover
                            {
                            background-color: $secondary;
                            color: color-yiq($secondary);
                            border-color: $secondary;
                        }
                    }
                }
            }   
        }
    }
}



.copyright-content{
    background-color: $bg-top-bar;
    color: color-yiq($bg-top-bar);
    padding-top: 10px;
    padding-bottom: 10px;

    .footer-image {
        .img-fluid{
            height: 20px;
        }
    }

    .footer-info{
        text-align: right;
        a{
            color:color-yiq($bg-top-bar);
        }
    }
}  