/*-----------
/----- Header Top bar -------- */

$bg-top-bar:  $primary-dark;
$bg-header-bar: $primary;
$bg-menu-bar:  $primary;
$bg-nav-bar: $primary;

.header-one{
    .bg-top-bar{
        background-color: $bg-top-bar;
    }
    .header-mini {   
        min-height: 26px;

        .navbar-lang{ 
            display: flex;

            .select-control{
                float: left;
                position: relative;
                
                // border-right: $border-width solid $border-color;
                width: 95px;
                

                .form-control{
                    background-color: transparent;
                    width: 100%;
                    color: color-yiq($bg-top-bar);
                }
                &::before{
                    font-family:"Font Awesome 5 Free";
                    font-weight: 900; /* regular style/weight */
                    content: "\f0d7";
                    position: absolute;
                    top: 7px;
                    right: 15px;
                    color: color-yiq($bg-top-bar);
                }
            }
            .select-control.currency{
                width: 75px;
            }
        }
        .navbar{
            padding: 0;
            
            .navbar-collapse {
                justify-content: flex-end;            
                .nav-item{
                    .nav-avatar{
                        display:flex;                        
                        .avatar{
                            width: 20px;
                            height: 20px;
                            border-radius:200px;
                            margin-right: 7px;
                            overflow: hidden;
                            img{
                                display: block;
                            }
                        }
                        span{
                            color: color-yiq($bg-top-bar);
                            position: relative;
                            bottom: -1px;
                            right: -2px;
                        }
                    }
                    
                    .nav-link{
                        color: color-yiq($bg-top-bar);
                        font-size:$font-size-sm;                    
                        &:hover{
                        color: $body-color-secondary;
                        }
                        
                    }
                }
            }
        }
    }

    /*-----------
    /----- Header bar -------- */

    .bg-header-bar{
        background-color: $bg-header-bar;
    }
    .header-maxi{
        padding-top: 0.9375rem;
        padding-bottom: 0.9375rem;    
        .logo {
            font-family: 'Pasajero';
            color: color-yiq($bg-header-bar);
            font-size: 34px;
            display: flex;
            align-items: center;
            line-height: normal;
            padding-top: 2px;
            text-decoration: none;
            strong {
                color: $secondary;
            }
        }
    
    .form-inline {
        display: flex;
        flex-flow: row wrap;
        align-items: center;

        .search {
            background-color: $white;
            border-radius: $border-radius;

            position: relative;
            width: 100%;
            
            .select-control{
                float: left;
                position: relative;
                border-right: $border-width solid $border-color;
                width: 159px;
                height: 40px;

                .form-control{
                    height: 40px;
                    width: 100%;
                }
                &::before{
                    font-family:"Font Awesome 5 Free";
                    font-weight: 900; /* regular style/weight */
                    content: "\f0d7";
                    position: absolute;
                    top: 10px;
                    right: 15px;
                    color: $body-color;
                }
            }
            input {
                float: right;
                height: 40px;
                padding: 8px 12px 8px 12px;
                width: calc(100% - 159px);
                border-radius: 0;
                border: none;
                outline: none !important;
            }
            .btn-secondary {
                position: absolute;
                right: 1px;
                top: 1px;
                bottom: 1px;
                height: calc(100% - 2px);
                width: 46px;
            }          
        }
    } 
    
    .top-right-list {
            padding-left: 0;
            margin-bottom: 0;
            display: flex;
            justify-content: flex-end;
            align-items: center;
            .wishlist-header {
                margin-left: 28px;
            }
            li {
                position: relative;
                display: inline-block;
                list-style: none;
                > a {
                    display: flex;
                    align-items: center;

                    .fas {
                        font-size: 2.2rem;
                        color: color-yiq($bg-header-bar);
                    }
                    .fa-stack{
                        display: flex;
                        align-items: center;
                        
                        .fa-heart {
                            font-size: $font-size-base;
                            top: 18px;
                            color:$primary;
                        }
                    }
                    
                    
                    .badge {
                        position: absolute;
                        left:0;
                        top:0;
                        height: 17px;
                        width: 17px;
                        min-width: 17px;
                        font-size: $font-size-sm;
                        color: color-yiq($secondary);
                        border-radius: $border-radius;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        z-index: 99;
                        box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.2);
                    }
                    
                }
                .block {
                    margin-left: 10px;

                    .title{
                        display: block;
                        font-size: $font-size-base;
                        color: color-yiq($bg-header-bar);
                        font-weight: $font-weight-bold;
                        margin-bottom: 0;
                    }
                    .items{
                        display: block;
                        font-size: $font-size-base;
                        color: color-yiq($bg-header-bar);
                        margin-bottom: 0;
                    }
                }
            }
            .cart-header {
                float: right;
                margin-left: 28px;
                a:hover {
                    text-decoration: none;
                }
                a:after {
                    margin-left: 0.875em;
                    color: color-yiq($bg-header-bar);
                }
                .shopping-cart {
                    width: 270px;
                    border-radius: $border-radius;
                    border-color: $white;
                    padding-left: 10px;
                    padding-right: 10px;
                    z-index: 99999;

                    .shopping-cart-items {

                        padding-left: 0;
                        padding-top: 7px;
                        max-height: 320px;
                        overflow-y: auto;
                        

                        li:last-child{
                            margin-bottom: 0;
                            padding-bottom: 0;
                            border-bottom: none;
                        }
                        li {
                            float: left;
                            position: relative;
                            width: 100%;
                            margin-bottom: 10px;
                            border-bottom: $border-width solid $border-color;
                            padding-bottom: 10px;

                            .item-thumb {
                                position: absolute;
                                left: 0;
                                top: 0;

                                .icon {
                                    border: $border-width solid $white;
                                    background-color: $danger;
                                    display: flex;
                                    align-items: center;
                                    justify-content: center;
                                    position: absolute;
                                    right: -5px;
                                    top: -5px;
                                    height: 16px;
                                    width: 16px;
                                    padding: 3px;
                                    z-index: 9;
                                    border-radius: $border-radius;
                                    opacity: 1;
                                }
                                .image {
                                    width: 64px;
                                    height: 64px;
                                    border: $border-width solid $border-color;
                                    border-radius: $border-radius;
                                    overflow: hidden;
                                    
                                }
                            }
                            .item-detail{
                                float: left;
                                padding-left: 74px;
                                
                                .item-name {
                                    display: block;
                                    font-size: $font-size-base;
                                    white-space: normal;
                                    text-transform: capitalize;
                                }
                                .item-quantity {
                                    float: left;
                                    background-color: $bg-header-bar;
                                    color: color-yiq($bg-header-bar);
                                    font-size: $font-size-base;
                                    padding: 0px 6px;
                                    padding-bottom: 2px;
                                    border-radius: $border-radius;
                                    margin-right: 10px;
                                }
                                .item-price {
                                    float: right;                                  
                                    font-size: $font-size-base;
                                    color: $link-color;
                                    font-weight: 700;
                                    margin-right: 8px;
                                }
                            }
                        }
                        .tt-summary {
                            background-color: $border-color;
                            border-radius: $border-radius;
                            padding-bottom: 10px;
                            padding-top: 10px;
                            padding-left: 0;
                            p {
                                font-size: $font-size-base;
                                color:$link-color;
                                font-weight:$font-weight-bolder;
                                margin-bottom: 0;
                                padding-left: 20px;
                                padding-right: 20px;
                                margin-top: 5px;
                                white-space: normal;
    
                                span {
                                    float: right;
                                }
                            }
                        }
                        .buttons{
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            .btn{
                                width: 50%;
    
                                &:first-child{
                                    margin-right: 5px;
                                }
                                &:last-child{
                                    margin-left: 5px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
        

    //----- Menu bar -------- */
    .bg-menu-bar{
        background-color: $bg-menu-bar;
    }
    .bg-nav-bar{
        background-color: $bg-nav-bar;
        // width: 100%;
        // // padding-right: 15px;
        // // padding-left: 15px;
        // margin-right: auto;
        // margin-left: auto;
    }
    .header-navbar {
        
        nav{
            width: 100%;
            padding: 0;

            .home-icon{
                background-color: $secondary;
                color:color-yiq($secondary);
                text-align: center;
                padding: 8px 10px;
                font-size: $font-size-sm;
                border-radius: $border-radius;
                &:hover{
                    color:color-yiq($secondary);
                }
            } 
            .navbar-collapse{
                ul{
                    .mega-dropdown{
                        position: static !important;
                        .nav-link{
                            position:relative;
                        }
                    
                        .mega-dropdown-menu[aria-expanded="true"]{
                            display:flex !important;
                        }
                        .mega-dropdown-menu {
                            
                            border-bottom: $border-width solid $border-color !important;
                            padding-bottom: 15px !important;
                            width: 100%;
                            box-shadow: none;

                            .dropdown-header {
                                padding-left:0;
                                font-size: $font-size-base;
                                font-weight: 500;
                                
                                a{
                                    color:color-yiq($light);
                                    font-size: $font-size-base;
                                    font-weight:$font-weight-bold;
                                    padding:0;
                                    
                                    &:hover, &:focus{
                                        color:theme-color("$secondary");
                                    }
                                }
                            }
                            .underline{
                                border-bottom:$border-width solid $border-color;
                                margin-bottom:10px;
                                color: $link-color;
                                font-size: $font-size-lg;
                            }
                        
                            .col-md-3{
                                ul{
                                    margin: 0;
                                    padding: 0;
                                    .carousel{
                                        .carousel-indicators{
                                            display: none;
                                        }
                                        .carousel-item {
                                            padding-bottom: 5px;
                                            .fa{
                                                color: $secondary;
                                                display: flex;
                                                align-items: center;
                                                justify-content: center;
                                                height: 40px;
                                                width: 40px;
                                                border-radius: 200px;
                                                border: $border-width solid $border-color;
                                                text-decoration: none;
                                                margin-left: 5px;
                                                margin-right: 5px;
                                                position: absolute;
                                                right: 0;
                                                z-index: 99;
                                                .badge {
                                                    position: absolute;
                                                    right: -5px;
                                                    top: 0;
                                                    border-radius: 50px;
                                                }
                                                .badge-secondary {
                                                    background-color: $secondary;
                                                    font-family:$font-family-base;
                                                    color: color-yiq($secondary);
                                                    border-radius: $border-radius;  
                                                    position: absolute;
                                                    top: 0;
                                                    left: 67%;
                                                    width: 16px;
                                                    height: 16px;
                                                    
                                                }
                                            }
                                            .category-tags{
                                                font-size: $font-size-sm;
                                                color:$body-color-secondary;
                                            }
                                            a {
                                                display: block;
                                                overflow: hidden;
                                                img {
                                                    width: 100%;
                                                }
                                            }
                                            
                                        h5 {
                                                display: flex;
                                                justify-content: flex-start;
                                                align-items: center;
                                                font-size: $font-size-base;
                                                color: $black;
                                                min-height: 20px;
                                                max-height: 30px;
                                                text-decoration: none;
                                            }
                                            
                                            .block {                                          
                                                display: flex;                                        
                                                align-items: center;
                                                .price {                                              
                                                    display: flex;                                                
                                                    justify-content: center;                                              
                                                    align-items: center;
                                                    font-size: ($font-size-lg + .25rem);
                                                    font-weight:$font-weight-bolder;
                                                    color: $secondary;
                                                    margin-right: 15px;
                                                    .line-through {
                                                        font-size: $font-size-lg;
                                                        text-decoration: line-through;
                                                        font-weight:$font-weight-bold;
                                                        margin-right: 10px;
                                                        color:$body-color-secondary;
                                                    }
                                                }  
                                            }
                                        
                                        }
                                    }
                                
                            
                                }
                            }
                            .menu-flex{
                                display: flex;
                                ul{
                                    list-style: none;
                                
                                li{
                                    position: relative;
                                    a{
                                        text-decoration: none;
                                        outline: none;

                                    }
                                }
                            }
                            .dropdown-header {
                                padding-left: 0;
                                    a {
                                        
                                        font-size: $font-size-lg;
                                        font-weight:$font-weight-bold;
                                        padding: 0;
                                    }
                                }
                            }
                        }
                            
                    }
                    .dropdown:hover > .dropdown-menu{
                        display: block;
                    }

                    li{                      
                        padding-right: 33px;
                        a{
                            
                           
                            .badge-secondary{
                            
                                position: absolute;
                                top: -5px;
                                left: 0;
                                border-radius: $border-radius;

                                &:after {
                                    content: '';
                                    position: absolute;
                                    left: 23px;
                                    top: 12px;
                                    width: 0;
                                    height: 0;
                                    border: 3px solid transparent;
                                    border-top: 10px solid $secondary;
                                    transform: rotate(-28deg);
                                }
                            }
                        }
                        

                        .dropdown-menu{
                            margin: 0;
                            padding: 0;
                            border-radius: $border-radius;
                            border-color: $border-color;
                            border-bottom-color: transparent;
                            
                            .dropdown-item{
                                padding: 8px 1rem;                            
                            }
                            
                        }

                    }
                    .nav-item{
                        .nav-link{
                        font-size: $font-size-base;
                        color: color-yiq($bg-menu-bar);
                        font-weight:$font-weight-bold;
                        text-transform: uppercase;
                        line-height: normal;
                    }
                    } 
                    li:last-child{
                        position: absolute;
                        right: 0;
                        padding: 0;
                        a{
                            padding-right: 0;
                        }
                        span{
                            float: left;
                            background-color: $secondary;
                            color: color-yiq($secondary);
                            padding-right: 5px;
                            padding-left: 5px;
                            border-radius: $border-radius;
                            margin-right: 15px;
                        }
                    
                    }

                }
            }
        }

    } 
}

.dropdown-submenu{
    position: relative;

    .fas{
        font-size: 13px;
        color: $body-color-secondary;
        margin-top: 5px;
        float: right;
    }

    .dropdown-menu {
        top: 0;
        left: 100%;
        margin-top: -1px;
    }

    &:hover{
        > .dropdown-menu{
            display: block;
        }
    }
}




